  padding-left: 14%;
}

.fayssal {
  position: relative;
  font-size: 30px;
  margin-left: 2px;

  -webkit-animation: color-change 2s infinite, size-change 2s infinite;

          animation: color-change 2s infinite, size-change 2s infinite;
}

@-webkit-keyframes color-change {
  100% {
    color: rgb(243, 243, 243);
  }
  50% {
    color: rgb(171, 97, 255);
  }
  0% {
    color: rgb(243, 243, 243);
  }
}

@keyframes color-change {
  100% {
    color: rgb(243, 243, 243);
  }
  50% {
    color: rgb(171, 97, 255);
  }
  0% {
    color: rgb(243, 243, 243);
  }
}
@-webkit-keyframes size-change {
  100% {
    bottom: 10px;
  }
  50% {
    bottom: 0px;
  }
  0% {
    bottom: 10px;
  }
}
@keyframes size-change {
  100% {
    bottom: 10px;
  }
  50% {
    bottom: 0px;
  }
  0% {
    bottom: 10px;
  }
}

