#typing {
  color: rgb(191, 169, 243);
  float: left;

  overflow: hidden;
  white-space: nowrap;
  -webkit-animation: typing 3s steps(22) 1s infinite alternate;
          animation: typing 3s steps(22) 1s infinite alternate;
}
#typing span {
  color: rgb(243, 243, 243);
}

#crow {
  float: left;

  color: rgb(220, 189, 245);

  font-size: 30px;
  -webkit-animation: crow 0.5s linear 0s infinite;
          animation: crow 0.5s linear 0s infinite;
}

@-webkit-keyframes typing {
  from {
    width: 4ch;
  }
  to {
    width: 24.5ch;
  }
}

@keyframes typing {
  from {
    width: 4ch;
  }
  to {
    width: 24.5ch;
  }
}
@-webkit-keyframes crow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes crow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

