*{
    margin: 0;
    padding: 0;
}
html{
    overflow-x: hidden;
    
}


body{
    box-sizing: border-box;
    font-family: 'Montserrat Alternates', sans-serif;
    overflow-y: auto;
    height: auto;
    transition: all 0.5s;
    overflow-x: hidden;
    background-color: rgb(248, 248, 248);

}
p {
	margin-bottom: 0px;
    font-family: 'Montserrat Alternates', sans-serif;
	font-size: 17px;
	color: #252525;
	line-height: 25px;
}
a {
	text-decoration: none!important;
}

/******* styling scrollbars ******/

::-webkit-scrollbar{
    width: 6px;
    height: 1px;
}

::-webkit-scrollbar-track{
    box-shadow: inset 0 0 5px #a5aaad;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb{
    background: #742a96;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover{
    background: #a5aaad;
}







